<template>
  <div class="stylelib-box">
    <top></top>
    <div class="admin-box main-top">
      <Tabs value="cate">
        <TabPane
          label=""
          name="cate">
          <Button
            class="mb"
            type="primary"
            @click="createCate">
            
          </Button>
          <Table
            border
            :columns="cateColumns"
            :data="cateList"></Table>
        </TabPane>
        <TabPane
          label=""
          name="tpl">
          <tpl :cate="cateList"></tpl>
        </TabPane>
      </Tabs>
    </div>
    <!--  -->
    <Modal
      v-model="update"
      title=""
      footer-hide>
      <Form
        ref="cateValidate"
        :model="cateValidate"
        :rules="cateRuleValidate"
        :label-width="120">
        <FormItem
          label=""
          prop="name">
          <Input
            v-model="cateValidate.name"
            placeholder="" />
        </FormItem>
        <FormItem
          label="()"
          prop="enname">
          <Input
            v-model="cateValidate.enname"
            placeholder="" />
        </FormItem>
        <FormItem
          label=""
          prop="hot">
          <Input
            v-model="cateValidate.hot"
            placeholder="" />
        </FormItem>
        <FormItem>
          <Button
            type="primary"
            @click="handleCate('cateValidate')">
            {{ isCreate ? '' : '' }}
          </Button>
          <!-- <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button> -->
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import top from '@/components/framework/top'
import tpl from './stylelib-tpl'
import { ajax } from '@/util/ajax'
export default {
  components: {
    top,
    tpl
  },
  data() {
    const validateEnglishName = (rule, value, callback) => {
      let re = new RegExp('^[a-zA-Z]+$')
      if (value === '') {
        callback(new Error(''))
      } else if (!re.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      update: false,
      cateColumns: [
        {
          title: 'ID',
          key: 'id'
        },
        {
          title: '',
          key: 'name',
          render: (h, params) => {
            return h('div', [
              h('Icon', {
                props: {
                  type: 'person'
                }
              }),
              h('strong', params.row.name)
            ])
          }
        },
        {
          title: '',
          key: 'enname'
        },
        {
          title: '',
          key: 'hot'
        },
        {
          title: '',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.editCate(params.row)
                    }
                  }
                },
                ''
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.removeCate(params.row)
                    }
                  }
                },
                ''
              )
            ])
          }
        }
      ],
      cateList: [],
      cateValidate: {
        name: '',
        enname: '',
        hot: null
      },
      cateRuleValidate: {
        name: [
          {
            required: true,
            message: '28',
            trigger: 'blur',
            min: 2,
            max: 8
          }
        ],
        enname: [
          {
            required: true,
            validator: validateEnglishName,
            trigger: 'blur'
          }
        ]
      },
      isCreate: false
    }
  },
  created() {
    this.getCate()
  },
  methods: {
    // 
    async getCate() {
      let res = await ajax({
        urlKey: '/api/template/tag/list',
        methods: 'POST',
        data: {}
      })
      if (res && res.status == 1) {
        this.cateList = res.data
      } else {
        this.$Message.error(res.data)
      }
    },
    editCate(row) {
      this.isCreate = false
      this.cateValidate = { ...row }
      this.update = true
    },
    removeCate(row) {
      this.$Modal.confirm({
        title: '',
        content: `<p>,${row.name}?</p>`,
        onOk: () => {
          ajax({
            urlKey: '/api/template/tag/delete',
            methods: 'POST',
            data: {
              tag_id: row.id
            }
          }).then(res => {
            if (res.status === 1) {
              this.$Message.success('!')
              this.getCate()
            } else {
              this.$Message.error(res.msg)
            }
          })
        },
        onCancel: () => {}
      })
    },
    createCate() {
      this.$refs.cateValidate.resetFields()
      this.isCreate = true
      this.update = true
    },
    handleCate(name) {
      const fd = {}
      let url = '/api/template/tag/create'
      fd.name = this.cateValidate.name
      fd.hot = this.cateValidate.hot || 1
      fd.enname = this.cateValidate.enname
      if (!this.isCreate) {
        fd.tag_id = this.cateValidate.id
        url = '/api/template/tag/update'
      }
      this.$refs[name].validate(valid => {
        if (valid) {
          ajax({
            urlKey: url,
            methods: 'POST',
            data: fd
          }).then(res => {
            if (res.status === 1) {
              this.$Message.success('Success!')
              this.update = false
              this.getCate()
            } else {
              this.$Message.error(res.msg)
            }
          })
        } else {
          this.$Message.error('Fail!')
        }
      })
    }
  }
}
</script>

<style lang="less">
.stylelib-box {
  .admin-box {
    padding: 20px;
    .mb {
      margin-bottom: 20px;
    }
  }
}
</style>
