<template>
  <div class="info-modal-window">
    <Form
      class="filter"
      :label-width="76"
      @submit.native.prevent>
      <FormItem label="">
        <rd-multi-radio-button
          v-model="modalWidth"
          :button-list="modalWidths"></rd-multi-radio-button>
      </FormItem>
      <FormItem label="">
        <rd-multi-radio-button
          v-model="headers"
          :button-list="modalHeaders"></rd-multi-radio-button>
      </FormItem>
      <Button
        type="primary"
        @click="openModal">
        Modal
      </Button>
    </Form>
    <Modal
      v-model="modalVisible"
      :transition-names="[]"
      class-name="rd-info-config-modal"
      :width="modalWidth[0]"
      @on-cancel="handleCance">
      <template #close>
        <span></span>
      </template>
      <!--  -->
      <template #header>
        <div class="header-wrap">
          <div class="header">
            <span
              v-if="isSingleTitle"
              class="single-title">
              {{ newTitles }}
            </span>
            <template v-else>
              <span
                v-for="title in newTitles"
                :key="title.value"
                class="title"
                :class="{ checked: title.value === multiTitleChecked }"
                @click="changeMultiTitleChecked(title)">
                {{ title.name }}
              </span>
            </template>
          </div>
          <!--  -->
          <Icon
            size="12"
            class="header-close"
            custom="iconfont ry-icon-close-tanch"
            @click="handleCance" />
        </div>
      </template>
      <!--  -->
      <div class="content">
        <Form
          :label-width="116"
          class="form"
          @submit.native.prevent>
          <div class="module-header"></div>
          <FormItem label="1-">
            <rd-multi-radio-button
              v-model="formData.radio1"
              :disabled="handleDisabled"
              :button-list="radioList"></rd-multi-radio-button>
          </FormItem>
          <FormItem label="1-">
            <rd-multi-radio-button
              v-model="formData.radio2"
              :button-list="radio2List"></rd-multi-radio-button>
            <div class="tips">1-</div>
          </FormItem>
          <FormItem label="1-">
            <rd-multi-radio-button
              v-model="formData.radio3"
              :button-list="multiList"></rd-multi-radio-button>
          </FormItem>
          <FormItem label="1-">
            <rd-multi-radio-button
              v-model="formData.radio4"
              :button-list="multiList"></rd-multi-radio-button>
            <div class="tips">1-1-1-</div>
          </FormItem>
          <FormItem label="1-">
            <rd-multi-radio-button
              v-model="formData.radio5"
              :button-list="statusList"></rd-multi-radio-button>
          </FormItem>
          <div class="module-split-line"></div>
          <div class="module-header"></div>
          <FormItem label="2-">
            <Select
              v-model="formData.select1"
              :disabled="radioSelectDisabled"
              style="width: 400px">
              <Option
                v-for="el in radioList"
                :key="el.value"
                :value="el.value"
                :label="el.label"></Option>
            </Select>
          </FormItem>
          <FormItem label="2-">
            <Select
              v-model="formData.select2"
              :max-tag-count="2"
              :max-tag-placeholder="setMaxTagPlaceholder"
              multiple
              style="width: 400px">
              <Option
                v-for="el in selectMultiOptionList"
                :key="el.value"
                :value="el.value"
                :disabled="el.disabled"
                :label="el.label"></Option>
            </Select>
          </FormItem>
          <FormItem
            required
            label="3-"
            :rules="inputValidate()">
            <Input
              v-model="formData.input1"
              placeholder=""
              style="width: 400px"></Input>
          </FormItem>
          <FormItem label="3--">
            <Input
              v-model="formData.input3"
              show-word-limit
              :maxlength="30"
              placeholder=""
              style="width: 400px"></Input>
          </FormItem>
          <FormItem label="3-">
            <div
              class="input-echo"
              style="width: 400px">
              {{ formData.input2 }}
            </div>
          </FormItem>
          <FormItem
            label="4-"
            required>
            <div class="bid">
              <InputNumber
                v-model="formData.bid1"
                :active-change="false"
                style="width: 184px; margin-right: 10px"
                placeholder=""
                :precision="2"
                :step="0.01"
                :max="9999.99"
                :min="0.01" />
              <span></span>
            </div>
          </FormItem>
          <FormItem
            label="4-"
            :rules="bidRangeValidate()">
            <div class="flex-start">
              <div class="range">
                <InputNumber
                  v-model="formData.bid2.min"
                  :active-change="false"
                  style="width: 184px"
                  placeholder=""
                  :precision="2"
                  :step="0.01"
                  :max="9999.99"
                  :min="0.01" />
                <span></span>
                <InputNumber
                  v-model="formData.bid2.max"
                  :active-change="false"
                  style="width: 184px"
                  placeholder=""
                  :precision="2"
                  :step="0.01"
                  :max="9999.99"
                  :min="0.01" />
              </div>
              <div style="margin-left: 10px"></div>
            </div>
          </FormItem>
          <FormItem label="4-">
            <div class="range">
              <DatePicker
                v-model="formData.date1.start"
                style="width: 184px"
                format="yyyy-MM-dd"
                :clearable="false"
                placeholder=""></DatePicker>
              <span></span>
              <DatePicker
                v-model="formData.date1.end"
                style="width: 184px"
                format="yyyy-MM-dd"
                :clearable="false"
                placeholder=""></DatePicker>
            </div>
          </FormItem>
          <FormItem label="5-">
            <Button
              :disabled="buttonDisabled"
              type="default"
              class="input-config-btn">
              <Icon
                style="margin-right: 3px"
                :custom="`iconfont ${formData.info1.icon}`"
                size="12"></Icon>
              <span>{{ formData.info1.text }}</span>
            </Button>
          </FormItem>
          <FormItem required>
            <template #label>
              <span class="label">
                <span>5-</span>
                <Tooltip
                  :transfer="true"
                  placement="top"
                  max-width="200"
                  theme="light"
                  content="5-5-">
                  <Icon
                    type="ios-help-circle-outline"
                    size="16"
                    class="cursor-pointer icon-question tip-icon"></Icon>
                </Tooltip>
              </span>
            </template>
            <div class="radio-info">
              <rd-multi-radio-button
                v-model="formData.info2.value"
                style="margin-bottom: 8px"
                :button-list="infoRadioList"></rd-multi-radio-button>
              <div
                class="input-config-show-info"
                :class="{ disabled: configShowInfoDisabled }">
                <span class="text">{{ formData.info2.text }}</span>
                <span class="config-btn"></span>
              </div>
            </div>
          </FormItem>
        </Form>
      </div>
      <!--  -->
      <template #footer>
        <div class="footer">
          <Button
            style="width: 104px"
            @click="handleCance">
            
          </Button>
          <Button
            type="primary"
            style="width: 104px"
            :loading="modalLoading"
            @click="handleCance">
            
          </Button>
        </div>
      </template>
    </Modal>
  </div>
</template>
<script>
import {
  isEqualObject,
  radioList,
  radio2List,
  statusList,
  multiList,
  infoRadioList
} from './../utils'

export default {
  data() {
    return {
      // 
      headers: ['0'],
      modalHeaders: [
        {
          label: '',
          value: '0',
          isMulti: false
        },
        {
          label: '',
          value: '1',
          isMulti: false
        }
      ],
      // 
      multiTitleChecked: '1',
      // modal
      modalWidth: [840],
      modalWidths: [
        {
          label: '640px',
          value: 640,
          isMulti: false
        },
        {
          label: '720px',
          value: 720,
          isMulti: false
        },
        {
          label: '840px',
          value: 840,
          isMulti: false
        },
        {
          label: '960px',
          value: 960,
          isMulti: false
        },
        {
          label: '1080px',
          value: 1080,
          isMulti: false
        }
      ],
      modalVisible: true,
      modalLoading: false,
      // 
      formData: {
        // 1-radio
        radio1: ['beijing'],
        radio2: ['pingguo'],
        radio3: ['2', '3'],
        radio4: ['2'],
        radio5: ['close'],
        // 2-select
        select1: 'beijing',
        select2: ['beijing', 'shanghai'],
        // 3-input
        input1: '',
        input2: '',
        input3: '',
        // 4-bid
        bid1: null,
        bid2: {
          min: null,
          max: null
        },
        // 4-date
        date1: {
          start: '',
          end: ''
        },
        // 5-info-config
        info1: {
          type: 'select',
          text: '',
          icon: 'ry-icon-sousuo'
        },
        // 5-info-config-showInfo
        info2: {
          type: 'showInfo',
          text: '0',
          icon: 'ry-icon-sousuo',
          value: ['renqun']
        }
      },
      // 
      radioList,
      radio2List,
      statusList,
      multiList,
      infoRadioList,
      // 
      nameLongList: [
        {
          label: ',,',
          value: 'chaoxian',
          isMulti: false
        }
      ]
    }
  },
  computed: {
    // 
    isSingleTitle() {
      return isEqualObject(this.headers, ['0'])
    },
    // 
    newTitles() {
      if (!this.isSingleTitle) {
        return [
          {
            name: '1',
            value: '1'
          },
          {
            name: '2',
            value: '2'
          },
          {
            name: '3',
            value: '3'
          }
        ]
      }
      return ''
    },
    // 
    radioSelectDisabled() {
      return isEqualObject(this.formData.radio5, ['open'])
    },
    // 
    buttonDisabled() {
      return isEqualObject(this.formData.select1, 'chaoxian')
    },
    // 
    configShowInfoDisabled() {
      return isEqualObject(this.formData.info2.value, ['renqun_zuhe'])
    },
    // 
    selectMultiOptionList() {
      const disableds = { guangzhou: true }
      let arr = JSON.parse(JSON.stringify(this.radioList))
      arr = arr.concat(this.nameLongList)
      return arr.map(val => Object.assign(val, { disabled: disableds[val.value] || false }))
    }
  },
  methods: {
    // 
    changeMultiTitleChecked(val) {
      this.multiTitleChecked = val.value
    },
    // 
    handleDisabled(val) {
      return val.value === 'notknown'
    },
    // 
    setMaxTagPlaceholder() {
      return this.formData.select2.length
    },
    openModal() {
      this.modalVisible = true
    },
    handleCance() {
      this.modalVisible = false
    },
    // 
    inputValidate() {
      return [
        {
          trigger: 'change',
          validator: () => {
            return new Promise((resolve, reject) => {
              let value = this.formData.input1
              if (!value) {
                reject(new Error(''))
                return
              }
              if (value.length > 20) {
                reject(new Error('20'))
                return
              }
              resolve()
            })
          }
        }
      ]
    },
    bidRangeValidate() {
      return [
        {
          trigger: 'change',
          validator: () => {
            return new Promise((resolve, reject) => {
              let { min, max } = this.formData.bid2
              if (!min || !max) {
                reject(new Error(''))
                return
              }
              if (min > max) {
                reject(new Error(''))
                return
              }
              resolve()
            })
          }
        }
      ]
    }
  }
}
</script>
<style scoped lang="less">
@import './index.less';
</style>