/**
 * 
 * @param obj1
 * @param obj2
 */
export const isEqualObject = (obj1, obj2) => {
  let d1 = JSON.stringify(obj1)
  let d2 = JSON.stringify(obj2)
  return d1 === d2
}

/**
 * js
 * @param value
 */
export const isObject = value => {
  return Object.prototype.toString.call(value) === '[object Object]'
}

