<template>
  <main class="main">
    <h2>{{ searchValue }}{{ sorts[0] && sorts[0].value }}</h2>
    <div class="main-tabs">
      <RadioGroup
        v-model="selSort"
        type="button">
        <Radio
          v-for="(i, j) in sorts"
          :key="j"
          :label="i.name">
          {{ i.name }} {{ i.value }}
        </Radio>
      </RadioGroup>
    </div>
    <div
      v-if="details.length"
      class="main-conent">
      <card-details
        v-for="(option, index) of details"
        :key="index"
        :data="option"></card-details>
    </div>
    <div
      v-else
      class="main-empty">
      
    </div>
  </main>
</template>
<script>
import { ajax } from '@/util/ajax'

import cardDetails from './card-details'

export default {
  components: { cardDetails },
  data() {
    return {
      // 
      selSort: '',
      all: [{ name: '', value: 0 }],
      sorts: [],
      resData: [],
      // "", "", ""
      componentsTagType: [
        {
          label: '',
          value: 'file'
        },
        {
          label: '',
          value: 'code'
        },
        {
          label: '',
          value: 'usage'
        }
      ]
    }
  },
  computed: {
    searchValue() {
      return this.$route.query.q
    },
    menuData() {
      return this.$store.state.menuData
    },
    // 
    details() {
      let list = []
      // 
      if (!this.sorts.length || !this.resData.length) {
        return list
      }
      let index = this.sorts.findIndex(item => {
        return item.name === this.selSort
      })
      if (!index) {
        // 
        list = []
        this.resData.forEach((option, i) => {
          if (option.length) {
            list = list.concat(this.filterData(i, option))
          }
        })
      } else {
        list = this.filterData(index - 1, this.resData[index - 1])
      }
      return list
    }
  },
  watch: {
    // 
    '$route.params.search': {
      handler() {
        this.getAjax()
      },
      deep: true
    },
    menuData: function () {
      this.init()
    }
  },
  mounted() {
    this.init()
    this.getAjax()
  },
  methods: {
    init() {
      if (!this.menuData || !Object.keys(this.menuData).length) {
        return false
      }
      let list = this.menuData.map(option => {
        return {
          name: option.name,
          value: 0
        }
      })
      this.sorts = this.all.concat(list)
    },
    // 
    filterData(index, arr) {
      let list = []
      // 2 
      let clildren = this.menuData[index]
      if (clildren) {
        this.getArrangementData(arr).forEach(item => {
          let path = `/${clildren.path}/`
          let secondLevel = ''
          let name = ''
          let describe = ''
          switch (clildren.name) {
            case '':
              path = `/stylelib-detail/${item.enname}/stylelib/${item.id}`
              secondLevel = item.name
              name = item.title
              describe = item.desc
              break
            case '':
              path = path + item.text + '/' + this.componentsTagType[item.tagTypeIndex].value
              secondLevel = this.componentsTagType[item.tagTypeIndex].label
              name = item.label + (item.text || '')
              describe = item.content
              break
            default:
              path = path + item.name
              secondLevel = ''
              name = item.label + (item.text || '')
              describe = item.content
          }
          list.push({
            parent: clildren.name, // ""
            children: secondLevel, // 
            name, // 
            describe, // 
            path
          })
        })
      }
      return list
    },
    /**
     *  
     *  params : href  data menuJSONlistname
     *  // let mac = [];
        // this.getChildName(item.name, clildren.child, mac);
     */
    // getChildName(href, data, list) {
    //   let idx = false;
    //   for (let i = 0; i < data.length; i++) {
    //     if (data[i].href === href) {
    //       list.push(data[i].name);
    //       idx = true;
    //     } else if (data[i].child.length && !idx) {
    //       this.getChildName(href, data[i].child, list);
    //     }
    //   }
    // },
    /**
     *  [[],[],[]]=>[{}]
     */
    getArrangementData(data) {
      let list = []
      data.forEach((item, index) => {
        if (item instanceof Array) {
          item.forEach(option => {
            option.tagTypeIndex = index
            list.push(option)
          })
        } else {
          list.push(item)
        }
      })
      return list
    },
    /**
     * 
     */
    getAjax() {
      this.selSort = ''
      ajax({
        urlKey: '/api/resource/query',
        methods: 'POST',
        data: {
          key: this.searchValue
        }
      }).then(res => {
        if (res.status === 1) {
          this.resData = res.data
          this.setData(res.data)
        } else {
          // 
          this.onClearData()
        }
      })
    },
    /**
     * 
     */
    onClearData() {
      this.sorts.forEach(item => {
        item.value = 0
      })
      this.resData = []
    },
    /**
     *   tabs 
     *  
     */
    setData(data) {
      let arr = []
      let len = 0
      data.forEach((item, index) => {
        let i = 0
        if (!item.length) {
          i = 0
        } else {
          i = this.getLength(item, index)
        }
        arr.push(i)
        len = len + i
      })
      // 
      arr.unshift(len)
      this.sorts.forEach((item, i) => {
        item.value = arr[i]
      })
    },
    /**
     *  
     *  [[],[],[[],[],[]],[],[]]  [,,[,,],,]
     *  
     *  parsms:  dataindex[]
     */
    getLength(data, index) {
      if (index === 2) {
        let i = 0
        data.forEach(k => {
          i = i + k.length
        })
        return i
      } else {
        return data.length
      }
    }
  }
}
</script>
